@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&family=JetBrains+Mono:wght@400;500;600&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

/* Prism.js Tomorrow Theme */
pre[class*="language-"] {
  color: #ccc;
  background: none;
  font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
  font-size: 1em;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;

  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;

  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}

.token.comment,
.token.block-comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: #999;
}

.token.punctuation {
  color: #ccc;
}

.token.tag,
.token.attr-name,
.token.namespace,
.token.deleted {
  color: #e2777a;
}

.token.function-name {
  color: #6196cc;
}

.token.boolean,
.token.number,
.token.function {
  color: #f08d49;
}

.token.property,
.token.class-name,
.token.constant,
.token.symbol {
  color: #f8c555;
}

.token.selector,
.token.important,
.token.atrule,
.token.keyword,
.token.builtin {
  color: #cc99cd;
}

.token.string,
.token.char,
.token.attr-value,
.token.regex,
.token.variable {
  color: #7ec699;
}

.token.operator,
.token.entity,
.token.url {
  color: #67cdcc;
}

.token.important,
.token.bold {
  font-weight: bold;
}
.token.italic {
  font-style: italic;
}

.token.entity {
  cursor: help;
}

/* Custom CSS Variables for Anthropic-inspired theme */
:root {
  --bg-primary: #fdfcfa;
  --bg-warm: #f7f5f3;
  --bg-chocolate: #8b6f47;
  --text-primary: #2d2926;
  --text-secondary: #6b6459;
  --accent-warm: #c4956c;
  --accent-gold: #d4a574;
  --shadow-warm: 0 4px 20px rgba(139, 111, 71, 0.08);
}

body {
  margin: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, var(--bg-primary) 0%, var(--bg-warm) 100%);
  color: var(--text-primary);
  min-height: 100vh;
}

code {
  font-family: 'JetBrains Mono', source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Enhanced typography */
.font-display {
  font-family: 'Inter', sans-serif;
  font-weight: 700;
  letter-spacing: -0.025em;
}

/* Warm shadows and cards */
.shadow-warm {
  box-shadow: var(--shadow-warm);
}

.bg-warm {
  background-color: var(--bg-warm);
}

.text-warm {
  color: var(--text-secondary);
}

/* Chocolate accent elements */
.accent-chocolate {
  background-color: var(--bg-chocolate);
}

.border-warm {
  border-color: var(--accent-warm);
}

/* Custom button styles */
.btn-warm {
  background: linear-gradient(135deg, var(--accent-warm) 0%, var(--accent-gold) 100%);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: 500;
  transition: all 0.2s ease;
  box-shadow: var(--shadow-warm);
}

.btn-warm:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 25px rgba(139, 111, 71, 0.15);
}

/* Enhanced code blocks */
pre[class*="language-"] {
  background: linear-gradient(135deg, #2a2a2a 0%, #1a1a1a 100%) !important;
  border: 1px solid rgba(196, 149, 108, 0.2);
  box-shadow: var(--shadow-warm);
}

/* Smooth scrolling */
html {
  scroll-behavior: smooth;
}
